import React from "react";
import DogBox from "../DogBox/DogBox";
import { Link } from "react-router-dom";

const WizardResultsRedux = ({ fetchedDogs }) => {
  return (
    <>
       {fetchedDogs.length === 0 ? 
        <div className="wizardButtonWrapper">
          <Link to="/adoptera">
            <button className="button">
              Gör om quizet
            </button>
          </Link>
        </div> : (
        fetchedDogs?.map((dog) => (
          <DogBox
            key={dog._id}
            hundnamn={dog.hundnamn}
            alder={dog.alder}
            ras={dog.ras}
            bild={dog.bild}
            id={dog._id}
            lank={dog.lank}
          />
        )))}
    </>
  );
};

export default WizardResultsRedux;
